*{
  margin: 0;
  padding: 0;
  transition: all 0.6s;
}

/* inicio estilos header */

.main-header{
  display: flex;
  justify-content: center;
  width: 100%;
  min-height: 80px;
  background-color: rgb(192, 211, 212);
}
.div-child-header{
  display: flex;
  width: 100%;
 
}
.title-header{
  display: flex;
  width: 30%;
  font-size: 50px;
  
}

.padre-buscar-producto{
  display: flex;
  padding: 0px 5px 0px 5px;
  width: 45%;
 
}
.input-buscar{
  display: flex;
  width:80%;
  margin: 1%;
  border: none;
  border-radius: 20px;
  padding: 2%;
}
.input-buscar:focus {
  outline:solid 1px  blue;
  background-color: rgb(241, 241, 241);
}

.input-buscar::placeholder{
  font-size: 20px;
  color: black;
}
.btn-buscar{
  display: flex;
  width: 20%;
  margin: 1%;
  font-size: 25px;
  border-radius: 20px;
  background-color: rgb(166, 235, 159);
  color: black;
  border: none;
  cursor: pointer;
  justify-content: center;
  align-items: center;
}

.btn-buscar:hover{
  box-shadow: 1px 3px 4px 5px rgb(78, 72, 72);
  color: black;
}


.div-car{
  display: flex;
  width: 15%;
 
}
.icon-car{
  display: flex;
  width: 20%;
  height: 50px;
  margin-top: 5%;
  margin-left: 10%;
  cursor: pointer;
  justify-content: center;
  align-items: center;
}
.count-car{
  display: flex;
  margin-top: 5%;
  color: black;
  font-size: 25px;
}

.carrito-open{
  display: flex;
  position: absolute;
  right: 1%;
  width: 30%;
  margin-top: 5%;
  min-height: 400px;
  background-color: rgb(156, 180, 159);
  padding: 5px;
  border-radius: 10px;
  border: solid 2px rgb(225, 226, 233);
  
}
.carrito-closed{
  display: none;
  width: 30%;
  margin-top: 1%;
  min-height: 400px;
  background-color: rgb(222, 237, 238);
  padding: 5px;
}



/*inicio estilos carrito*/
.car-content-padre{
  display: flex;
  flex-direction: column;
  width: 80%;
  margin: auto;
  height: 600px;
  border-radius: 20px;
  padding: 5px;
  overflow: scroll;
  
}

.car-content-child{
  background-color: rgba(82, 79, 119, 0.493);
  display: flex;
  flex-direction: column;
  width: 60%;
  padding: 10px;
  margin: auto;
  justify-content: center;
  align-items: center;
  margin-top: 2%;
  border-radius: 10px;
  border: solid 1px rgb(255, 255, 255);
}

.prod-quantity-carrito{
  display: flex;
  width: 100%;
  font-size: 20px;
  color: white;
  
}

.prod-quantity-reduce-carrito{
  display: flex;
  width: 10%;
  margin-left: 92%;
  justify-content: center;
  font-size: 20px;
  cursor: pointer;
  color: white;
}
.prod-quantity-reduce-carrito:hover{
  background-color: red;
}

.prod-title-carrito{
  display: flex;
  width: 90%;
  font-size: 15px;
  justify-content: center;
}
.prod-icon-carrito{
  display: flex;
  width: 30%;
  justify-content: center;
}
.prod-price-carrito{
  display: flex;
  width: 80%;
  justify-content: center;
}

.prod-remove-carrito{
  display: flex;
  width: 30%;
  font-size: 15px;
  justify-content: center;
  margin: auto;
  margin-top: 1%;
  margin-bottom: 3%;
  border-radius: 20px;
  background-color: rgb(230, 34, 34);
  color: white;
}
/*fin estilos carrito*/

.prod-btn-vaciar-carrito{
  display: flex;
  width: 30%;
  margin-left: 35%;
  margin-bottom: 2%;
  justify-content: center;
  min-height: 30px;
  background-color: red;
  color: white;
  font-size: 20px;
  border-radius: 5px;
  border: none;
}
.prod-btn-vaciar-carrito:hover{
box-shadow: 1px 3px 4px 5px black;
}

.prod-btn-comprar-carrito{
  display: flex;
  width: 30%;
  margin-left: 35%;
  margin-bottom: 2%;
  justify-content: center;
  min-height: 30px;
  background-color: rgb(90, 230, 78);
  color: white;
  font-size: 20px;
  border-radius: 5px;
  border: none;
}
.prod-btn-comprar-carrito:hover{
  box-shadow: 1px 3px 4px 5px black;
  }
  

.prod-total-carrito{
  display: flex;
  margin-top: 2%;
}

.titulo-carrito-vacio{
  display: flex;
  margin: auto;
}

/* fin estilos header */


/* inicio estilos produclist */
.main-container{
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  min-height: auto;
  background-color: rgb(211, 223, 224);
}

.child-container{
  width: 23%;
  background-color:rgb(186, 204, 204) ;
  margin: 8px;
  padding: 5px;
  justify-content: center;
  border-radius: 10px;
}

.prod-title{
  display: flex;
  font-size: 25px;
  justify-content: center;
  margin-bottom: 2%;
  
}

.prod-image{
  display: flex;
  width: 70%;
  height: 250px;
  border: solid 1px;
  margin: 10px;
  margin: auto;
  margin-bottom: 2%;
  margin-top: 3%;
}
.prod-description{
  display: flex;
  width: 60%;
  margin: auto;
  justify-content: center;
  align-content: center;
}
.prod-price{
  display: flex;
  margin-top: 2%;
  justify-content: center;
  font-family: Arial, sans-serif;
  font-size: 25px;
}

.prod-add{
  display: flex;
  width: 30%;
  font-size: 25px;
  justify-content: center;
  margin: auto;
  margin-top: 5%;
  margin-bottom: 3%;
  border-radius: 20px;
  padding: 5px;
  background-color: rgb(40, 226, 23);
  box-shadow: 1px 3px 4px 5px black;
  color: black;
  border: none;
  cursor: pointer;
}

.prod-add:hover{
  box-shadow: 1px 3px 4px 5px white;
  color: black;
}
/* fin estilos produclist */




